import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Form, Input } from 'antd';
import { getUser, getRankConfigId, createRankConfig, updateRankConfig } from 'services/rankConfig';
import { requiredRule } from 'utils/validateRules';

const initialValueDefault = {
  id: '',
  label: '',
};

@Form.create({ name: 'RankConfigFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
  };

  componentDidMount = async () => {
    const { match, form } = this.props;
    getUser()
      .then(res => {
        this.setState({
          token: res.data.accessToken,
        });
      })
      .catch(error => {
        console.log(error);
      });
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      const initialValue = await this.getDataById(match.params.id);
      form.setFieldsValue(initialValue);
    } else {
      form.setFieldsValue(initialValueDefault);
    }
  };

  getDataById = async id => {
    const res = await getRankConfigId(id);
    return { ...initialValueDefault, ...res.data };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async doc => {
    const { dataId, token } = this.state;
    try {
      if (!dataId) {
        await createRankConfig(doc, token);
      } else {
        await updateRankConfig(doc, token);
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm ứng viên">
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="Mã ngạch">
            {getFieldDecorator('id', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Chức danh">
            {getFieldDecorator('label', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <div style={{ textAlign: 'center' }}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={() => this.props.history.goBack()} />
          </div>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



